.favorite {
  position: relative;
  margin-top: -166px;
  padding-bottom: 40px;
  @include response(1200px) {
    margin-top: -140px;
    padding-bottom: 20px;
  }
  &::before {
    content: "";
    position: absolute;
    height: 240px;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f5f3f4;
    @include response(767px) {
      height: 665px;
    }
  }
  &__box {
    position: relative;
    background: #fff;
    border-radius: 10px;
    padding-top: 80px;
    padding-bottom: 65px;
    @include response(1200px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
  &__top {
    position: relative;
    margin-bottom: 95px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1200px) {
      display: block;
      margin-bottom: 40px;
    }
  }
  &__img {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__info {
    position: relative;
    flex: 0 0 492px;
    max-width: 492px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  &__title {
    margin: 0 0 20px;
  }
  &__text {
    margin-bottom: 40px;
    max-width: 400px;
    width: 100%;
    color: #7d7c7a;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &--mb30 {
      margin-bottom: 30px;
      @include response(1200px) {
        margin-bottom: 20px;
      }
    }
  }
  &__info-list {
    position: relative;
  }
  &__info-item {
    position: relative;
    background: #f5f3f4;
    border-radius: 6px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__info-item-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 10px 16px;
  }
  &__info-item-icon {
    position: relative;
    flex: 0 0 26px;
    width: 26px;
    height: 26px;
    margin-right: 12px;
  }
  &__info-item-title {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    padding-right: 20px;
    flex: 1 1 auto;
  }
  &__info-item-arrow {
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
    .active.favorite__info-item & {
      transform: scaleY(-1);
    }
  }
  &__info-item-body {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    position: relative;
    padding: 0 24px 16px 54px;
    display: none;
    .active.favorite__info-item & {
      display: block;
    }
    p {
      margin: 0;
    }
  }
  &__aside {
    position: relative;
    flex: 0 0 300px;
    max-width: 300px;
    width: 100%;
    border: 4px solid #f5f3f4;
    border-radius: 6px;
    padding: 26px;
    @include response(1200px) {
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__aside-img {
    margin-bottom: 20px;
    text-align: center;
  }
  &__aside-title {
    font-family: "Abril Fatface";
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 10px;
  }
  &__aside-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7d7c7a;
    margin-bottom: 20px;
  }
  &__aside-btn {
    width: 100%;
  }
  &__bottom {
    position: relative;
    display: flex;
    @include response(1200px) {
      display: block;
    }
  }
  &__bottom-left {
    position: relative;
    flex: 0 0 400px;
    max-width: 400px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  &__bottom-right {
    position: relative;
    flex: 0 0 834px;
    max-width: 834px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__all {
    position: relative;
    @include response(767px) {
      width: 100%;
    }
  }
  &__games {
    position: relative;
    display: flex;
    margin-left: -24px;
    @include response(1200px) {
      margin-left: -15px;
    }
    @include response(767px) {
      flex-wrap: wrap;
      margin-bottom: -15px;
    }
  }
  &__games-item {
    position: relative;
    flex: 0 0 calc(25% - 24px);
    max-width: calc(25% - 24px);
    width: 100%;
    margin-left: 24px;
    background: #f5f3f4;
    border-radius: 6px;
    height: 239px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: 0.3s ease-in-out;
    text-decoration: none;
    @include response(1200px) {
      height: 172px;
    }
    @include response(767px) {
      flex: 0 0 calc(50% - 15px);
      max-width: calc(50% - 15px);
      margin-left: 15px;
      margin-bottom: 15px;
    }
    @include desktop-hover() {
      transform: translateY(-3px);
    }
  }
  &__games-img {
    margin-bottom: 13px;
    @include response(1200px) {
      width: 60px;
      margin-bottom: 10px;
    }
  }
  &__games-item-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: #141414;
    @include response(1200px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}
