.results {
  position: relative;
  padding-top: 75px;
  padding-bottom: 160px;
  @include response(1200px) {
    padding-top: 40px;
    padding-bottom: 70px;
  }
  &__decor {
    position: absolute;
    left: 50%;
    bottom: 40px;
    transform: translateX(-50%);
    @include response(1200px) {
      left: 50px;
      transform: none;
      width: calc(100% - 100px);
      bottom: 20px;
    }
  }
  &__title {
    margin-bottom: 20px;
    text-align: center;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #7d7c7a;
    max-width: 405px;
    width: 100%;
    margin: 0 auto 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: center;
    @include response(1200px) {
      display: block;
      max-width: 450px;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__item {
    position: relative;
    text-decoration: none;
    flex: 0 0 33.333%;
    max-width: 33.333%;
    width: 100%;
    background: $red;
    min-height: 158px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include desktop-hover() {
      .results__item-title {
        text-decoration: underline;
      }
    }
    @include response(1200px) {
      max-width: 100%;
      min-height: 122px;
      padding-bottom: 30px;
      padding-top: 0;
    }
    &:nth-child(1) {
      border-radius: 8px 0 0 8px;
      @include response(1200px) {
        border-radius: 8px 8px 0 0;
      }
    }
    &:nth-child(2) {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #e50a14;
    }
    &:nth-child(3) {
      border-radius: 0 8px 8px 0;
      @include response(1200px) {
        border-radius: 0 0 8px 8px;
      }
    }
  }
  &__logo {
    margin-bottom: 2px;
  }
  &__item-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #fff;
    svg {
      flex: 0 0 auto;
      margin-right: 8px;
    }
  }
}
