body {
  height: 100%;
  font-family: "Lato", "Abril Fatface", sans-serif;
  line-height: 1;
  color: #141414;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper {
  position: relative;
  flex: 1 1 auto;
  max-width: 100%;
  overflow: hidden;
}

.main {
  position: relative;
  padding-top: 67px;
}

.container-small {
  position: relative;
  max-width: 1130px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1200px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.container {
  position: relative;
  max-width: 1280px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1300px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.container-full {
  position: relative;
  max-width: 1400px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1439px) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
