.footer {
  position: relative;
  background: #141414;
  padding-top: 80px;
  padding-bottom: 60px;
  @include response(1200px) {
    padding-top: 120px;
  }
  &__box {
    display: flex;
    @include response(1200px) {
      flex-direction: column-reverse;
      align-items: center;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 300px;
    max-width: 300px;
    width: 100%;
    margin-right: 100px;
    padding-top: 74px;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: 100%;
      margin-right: 0;
      padding-top: 50px;
    }
  }
  &__logo {
    position: relative;
    margin-bottom: 30px;
    @include response(1200px) {
      margin-bottom: 20px;
      text-align: center;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0 0 10px;
    display: flex;
    @include response(1200px) {
      justify-content: center;
    }
    li {
      position: relative;
      margin-right: 20px;
      &::before {
        content: "";
        position: absolute;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #fff;
        top: 50%;
        margin-top: -3px;
        right: -13px;
      }
      &:last-child {
        margin-right: 0;
        &::before {
          display: none;
        }
      }
    }
    a {
      display: block;
      text-decoration: none;
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: #fff;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
  &__copy {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    opacity: 0.4;
    @include response(1200px) {
      text-align: center;
    }
  }
  &__nav {
    position: relative;
    display: flex;
    @include response(1200px) {
      display: block;
      text-align: center;
      width: 100%;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  &__nav-cell {
    max-width: 220px;
    position: relative;
    margin-right: 100px;
    @include response(1200px) {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__nav-cell-title {
    font-family: "Abril Fatface";
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #fff;
    position: relative;
    padding-bottom: 18px;
    margin-bottom: 28px;
    @include response(1200px) {
      padding-bottom: 14px;
      margin-bottom: 20px;
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 40px;
      height: 4px;
      background: $red;
      @include response(1200px) {
        left: 50%;
        margin-left: -20px;
      }
    }
  }
  &__nav-list {
    position: relative;
    list-style: none;
    margin: 0 0 26px;
    padding: 0;
    &:last-child {
      margin-bottom: 0;
    }
    li {
      position: relative;
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      text-decoration: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #fff;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $red;
      }
    }
  }
  &__btn {
    position: relative;
    @include response(767px) {
      width: 100%;
    }
  }
}
