.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  font-family: "Lato";
  cursor: pointer;
  transition: 0.3s ease-in-out;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  background: #141414;
  padding: 8px 25px;
  border-radius: 6px;
  border: 0;
  @include desktop-hover() {
    background: $red;
  }
  @include response(1200px) {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}

.btn-2 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: "Lato";
  cursor: pointer;
  border: 0;
  transition: 0.3s ease-in-out;
  background: $red;
  border-radius: 6px;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  padding: 12px;
  @include desktop-hover() {
    background: #141414;
  }
}

.btn-3 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: "Lato";
  cursor: pointer;
  border: 1px solid #141414;
  transition: 0.3s ease-in-out;
  background: #fff;
  border-radius: 6px;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #141414;
  padding: 11px 24px;
  @include desktop-hover() {
    background: $red;
    color: #fff;
    border-color: $red;
    svg {
      fill: #fff;
    }
  }
  svg {
    fill: #141414;
    flex: 0 0 auto;
    margin-right: 8px;
    transition: 0.3s ease-in-out;
  }
}

.btn-4 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: "Lato";
  cursor: pointer;
  border: 2px solid #fff;
  transition: 0.3s ease-in-out;
  background: #141414;
  border-radius: 6px;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  padding: 6px 28px;
  @include response(1200px) {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  @include desktop-hover() {
    background: #fff;
    color: #141414;
  }
}
