.btn-top {
  position: absolute;
  bottom: 60px;
  right: 120px;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  width: 106px;
  cursor: pointer;
  @include response(1200px) {
    top: 40px;
    right: auto;
    left: 50%;
    margin-left: -53px;
    bottom: auto;
  }
  @include desktop-hover() {
    .btn-top__icon {
      transform: translateY(-3px);
    }
  }
  &__icon {
    position: relative;
    flex: 0 0 40px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    margin-right: 12px;
    transition: .3s ease-in-out;
    svg {
      fill: #fff;
    }
  }
}
