.reviews {
  position: relative;
  padding-top: 40px;
  padding-bottom: 80px;
  background: #f5f3f4;
  @include response(1200px) {
    padding-top: 20px;
    padding-bottom: 40px;
  }
  &__box {
    position: relative;
    display: flex;
    justify-content: space-between;
    @include response(1200px) {
      display: block;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 752px;
    max-width: 752px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  &__title {
    margin-bottom: 20px;
  }
  &__holder {
    position: relative;
  }
  &__label {
    position: relative;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    display: inline-block;
    vertical-align: top;
    padding: 4px 14px;
    border-radius: 6px 6px 0 0;
    @include response(1200px) {
      font-size: 10px;
      line-height: 10px;
      padding-left: 8px;
      padding-right: 8px;
    }
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    margin-bottom: 26px;
    background: #fff;
    border-radius: 6px;
    display: flex;
    align-items: center;
    padding: 14px 20px;
    @include response(767px) {
      padding: 12px 0;
    }
    > div,
    > a {
      padding-left: 26px;
      padding-right: 26px;
      position: relative;
      @include response(767px) {
        padding-left: 10px;
        padding-right: 10px;
      }
      &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 28px;
        background: #f5f3f4;
        top: 50%;
        margin-top: -14px;
        right: 0;
      }
      &:first-child {
        padding-left: 0;
        @include response(767px) {
          padding-left: 10px;
        }
      }
      &:last-child {
        padding-right: 0;
        @include response(767px) {
          padding-right: 10px;
        }
        &::before {
          display: none;
        }
      }
    }
    &:first-child {
      border-top-left-radius: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-img {
    flex: 0 0 166px;
    @include response(767px) {
      flex: 0 0 25%;
      .reviews__right & {
        flex: 0 0 calc(50% - 45px);
      }
    }
  }
  &__item-rate {
    flex: 0 0 115px;
    @include response(767px) {
      flex: 0 0 25%;
    }
  }
  &__item-rate-row {
    position: relative;
    display: flex;
    align-items: center;
    svg {
      flex: 0 0 auto;
      margin-right: 4px;
    }
  }
  &__item-rate-value {
    position: relative;
    font-weight: 400;
    font-size: 10px;
    line-height: 19px;
    display: flex;
    align-items: flex-start;
    font-family: "Abril Fatface";
  }
  &__item-rate-value-big {
    font-size: 16px;
    line-height: 22px;
    flex: 0 0 auto;
    margin-right: 2px;
  }
  &__item-rate-value-small {
    font-size: 12px;
    line-height: 18px;
    flex: 0 0 auto;
    margin-left: 2px;
  }
  &__item-text {
    font-weight: 500;
    font-size: 10px;
    line-height: 14px;
    color: #7d7c7a;
  }
  &__item-date {
    flex: 0 0 115px;
    @include response(767px) {
      flex: 0 0 25%;
    }
  }
  &__item-pay {
    flex: 0 0 144px;
    @include response(767px) {
      display: none;
    }
  }
  &__item-games {
    flex: 0 0 122px;
    @include response(767px) {
      flex: 0 0 90px;
    }
  }
  &__item-pay-list {
    list-style: none;
    padding: 0;
    margin: 0 0 2px;
    display: flex;
    align-items: center;
    li {
      flex: 0 0 20px;
      display: flex;
      margin-right: 4px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__item-btn-box {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include response(767px) {
      flex: 0 0 25%;
      .reviews__right & {
        flex: 0 0 calc(50% - 45px);
      }
    }
  }
  &__item-btn {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    color: #141414;
    @include response(1200px) {
      font-size: 12px;
      line-height: 12px;
      font-weight: 500;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 7px;
    }
  }
  &__right {
    position: relative;
    flex: 0 0 488px;
    max-width: 488px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
    }
  }
}
