.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 100;
  border-bottom: 1px solid #f5f3f4;
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1280px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1300px) {
      padding-left: 20px;
      padding-right: 20px;
    }
    @include response(1200px) {
      padding-top: 13px;
      padding-bottom: 13px;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 200px;
    max-width: 200px;
    width: 100%;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: none;
      width: auto;
    }
  }
  &__logo {
    position: relative;
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__center {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__nav {
    position: relative;
    @include response(1200px) {
      margin-bottom: 30px;
      flex: 1 1 auto;
    }
  }
  &__nav-list {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    @include response(1200px) {
      display: block;
      border-top: 2px solid #141414;
    }
    > li {
      position: relative;
      @include response(1200px) {
        border-bottom: 2px solid #141414;
      }
      &.menu-item-has-children {
        @include response(1200px) {
          &.active {
            > a {
              color: $red;
              &::after {
                border-top-color: $red;
                transform: scaleY(-1);
              }
            }
            > ul {
              display: block;
            }
          }
        }
        > a {
          &::after {
            content: "";
            display: inline-block;
            width: 0;
            height: 0;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-top: 6px solid #000;
            flex: 0 0 auto;
            margin-left: 6px;
            transition: 0.3s ease-in-out;
          }
        }
      }
      @include desktop-hover() {
        > a {
          color: $red;
          &::after {
            border-top-color: $red;
            transform: scaleY(-1);
          }
        }
        > ul {
          opacity: 1;
          visibility: visible;
          transform: translateY(-5px);
        }
      }
      > a {
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        position: relative;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        color: #141414;
        height: 66px;
        padding: 5px 20px;
        transition: 0.3s ease-in-out;
        @include response(1200px) {
          padding: 16px 0;
          height: auto;
          justify-content: space-between;
        }
        @include desktop-hover() {
          color: $red;
        }
      }
      > ul {
        list-style: none;
        padding: 15px 0;
        margin: 0;
        position: absolute;
        top: 100%;
        left: 0;
        width: 300px;
        z-index: 1;
        background: #fff;
        border-radius: 6px;
        box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
        transition: 0.2s ease-in-out;
        opacity: 0;
        visibility: hidden;
        transform: translateY(-15px);
        @include response(1200px) {
          opacity: 1;
          transform: none;
          visibility: visible;
          position: relative;
          box-shadow: none;
          top: auto;
          left: auto;
          padding: 0 0 20px;
          display: none;
        }
        li {
          position: relative;
        }
        a {
          display: block;
          text-decoration: none;
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          color: #141414;
          padding: 8px 20px;
          transition: 0.3s ease-in-out;
          @include desktop-hover() {
            color: $red;
          }
        }
      }
    }
  }
  &__right {
    position: relative;
    flex: 0 0 200px;
    max-width: 200px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: none;
      width: auto;
    }
  }
  &__btn {
    position: relative;
    @include response(1200px) {
      align-self: flex-start;
      min-width: 185px;
    }
  }
  &__mobile {
    display: none;
    @include response(1200px) {
      display: block;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      transform: translateX(-100%);
      opacity: 0;
      visibility: hidden;
      &.active {
        visibility: visible;
        opacity: 1;
        transform: none;
      }
    }
  }
  &__mobile-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#000, 0.5);
    opacity: 0;
    transition: .3s ease-in-out;
    .header__mobile.active & {
      opacity: 1;
    }
  }
  &__mobile-box {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    max-width: 400px;
    width: 100%;
    background: #fff;
    padding: 130px 35px 60px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    transition: .3s ease-in-out;
    transform: translateX(-100%);
    &.active {
      transform: translateX(0);
    }
    @include response(767px) {
      max-width: 100%;
    }
    @media screen and (max-width: 1200px) and (orientation: landscape) {
      padding-top: 40px;
    }
  }
  &__mobile-btn {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      background: #0b1014;
      border-radius: 2px;
      svg {
        fill: #fff;
      }
    }
  }
  &__mobile-close {
    position: absolute;
    top: 65px;
    right: 20px;
    width: 30px;
    height: 30px;
    border-radius: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #141414;
    @media screen and (max-width: 1200px) and (orientation: landscape) {
      top: 13px;
    }
  }
  &__mobile-title {
    position: relative;
    margin-bottom: 46px;
    font-family: "Abril Fatface";
    font-weight: 400;
    font-size: 40px;
    line-height: 54px;
    color: $red;
    @media screen and (max-width: 1200px) and (orientation: landscape) {
      margin-bottom: 20px;
    }
  }
}
