.first-screen {
  position: relative;
  padding-top: 167px;
  padding-bottom: 266px;
  @include response(1200px) {
    padding-top: 116px;
    padding-bottom: 200px;
  }
  &__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    max-width: 463px;
    width: 100%;
    margin: 0 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    &.title-page {
      color: #fff;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 26px;
    color: #fff;
    opacity: 0.8;
    max-width: 463px;
    width: 100%;
    margin-bottom: 86px;
    @include response(1200px) {
      margin-bottom: 40px;
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__list {
    display: flex;
    margin-left: -30px;
    @include response(767px) {
      margin-left: -15px;
      flex-wrap: wrap;
    }
  }
  &__link {
    position: relative;
    text-decoration: none;
    flex: 0 0 calc(25% - 30px);
    max-width: calc(25% - 30px);
    width: 100%;
    margin-left: 30px;
    background: rgba(229, 10, 20, 0.8);
    border: 2px solid #ff7d83;
    border-radius: 6px;
    padding: 18px;
    text-align: center;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      padding: 16px;
    }
    @include response(767px) {
      margin-left: 15px;
      margin-bottom: 15px;
      flex: 0 0 calc(50% - 15px);
      max-width: calc(50% - 15px);
    }
    @include desktop-hover() {
      transform: translateY(-3px);
      background: rgba(229, 10, 20, 1);
    }
  }
  &__link-img {
    margin-bottom: 10px;
    @include response(767px) {
      width: 40px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__link-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: #fff;
    @include response(1200px) {
      font-size: 14px;
      line-height: 18px;
      font-weight: 400;
    }
  }
}
