.news-home {
  position: relative;
  padding-top: 40px;
  padding-bottom: 74px;
  @include response(1200px) {
    padding-top: 20px;
    padding-bottom: 60px;
    background: #f5f3f4;
  }
  &::before {
    content: "";
    position: absolute;
    height: 230px;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f5f3f4;
    @include response(1200px) {
      background: #fff;
      bottom: auto;
      top: 0;
      height: 246px;
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;
    @include response(1200px) {
      display: block;
    }
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &__left {
    position: relative;
    display: flex;
    align-items: center;
    @include response(767px) {
      display: block;
    }
  }
  &__title {
    margin-right: 60px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 20px;
      text-align: center;
    }
  }
  &__text {
    position: relative;
    max-width: 397px;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #7d7c7a;
    @include response(767px) {
      text-align: center;
      max-width: 100%;
    }
  }
  &__controls {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__all {
    position: relative;
  }
  &__list {
    position: relative;
    display: flex;
    margin-left: -40px;
    @include response(1200px) {
      margin-left: -20px;
      flex-wrap: wrap;
    }
  }
}
.news {
  position: relative;
  flex: 0 0 calc(25% - 40px);
  max-width: calc(25% - 40px);
  width: 100%;
  margin-left: 40px;
  @include response(1200px) {
    flex: 0 0 calc(50% - 20px);
    max-width: calc(50% - 20px);
    margin-left: 20px;
  }
  @include response(767px) {
    flex: 0 0 calc(100% - 20px);
    max-width: calc(100% - 20px);
  }
  .news-home__list & {
    margin-bottom: 0;
    @include response(1200px) {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__img {
    position: relative;
    display: block;
    text-decoration: none;
    border-radius: 6px;
    margin-bottom: 16px;
    overflow: hidden;
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }
  }
  &__label {
    position: absolute;
    left: 12px;
    bottom: 10px;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    padding: 2px 4px;
    background: $red;
    border-radius: 2px;
    text-transform: uppercase;
    z-index: 1;
  }
  &__row {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
  }
  &__date {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #7d7c7a;
    margin-right: 20px;
    flex: 0 0 auto;
  }
  &__views {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #7d7c7a;
    svg {
      flex: 0 0 auto;
      margin-right: 6px;
      fill: #7d7c7a;
    }
  }
  &__title {
    display: block;
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #141414;
    transition: 0.3s ease-in-out;
    height: 44px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    @include desktop-hover() {
      color: $red;
    }
    p {
      margin: 0;
    }
  }
}
