.top-box {
  position: relative;
  background: #f5f3f4;
  padding-top: 80px;
  padding-bottom: 212px;
  min-height: 580px;
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__title {
    max-width: 636px;
    width: 100%;
    margin-bottom: 30px;
  }
  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    max-width: 666px;
    width: 100%;
    p {
      margin: 0;
    }
  }
}
