.title-page {
  position: relative;
  font-family: "Abril Fatface";
  font-weight: 400;
  font-size: 56px;
  line-height: 64px;
  color: #141414;
  @include response(1200px) {
    font-size: 40px;
    line-height: 54px;
  }
  span {
    color: $red;
  }
}

.title {
  position: relative;
  font-family: "Abril Fatface";
  font-weight: 400;
  font-size: 40px;
  line-height: 54px;
  color: #141414;
  @include response(1200px) {
    font-size: 28px;
    line-height: 38px;
  }
  span {
    color: $red;
  }
}
